/*
  Microsoft-style дизайн для GraniteH
  Строгий, профессиональный, без блоков, на полную ширину
*/

:root {
  /* Строгая цветовая палитра в стиле Microsoft */
  --primary-color: #0078d4;
  --primary-hover: #106ebe;
  --secondary-color: #323130;
  --text-primary: #323130;
  --text-secondary: #605e5c;
  --text-light: #8a8886;
  --background-white: #ffffff;
  --background-light: #faf9f8;
  --background-dark: #f3f2f1;
  --accent-blue: #0078d4;
  --accent-green: #107c10;
  --accent-orange: #ff8c00;
  --border-light: #edebe9;
  --border-medium: #d2d0ce;
  --shadow-light: 0 1.6px 3.6px 0 rgba(0, 0, 0, 0.132), 0 0.3px 0.9px 0 rgba(0, 0, 0, 0.108);
  --shadow-medium: 0 6.4px 14.4px 0 rgba(0, 0, 0, 0.132), 0 1.2px 3.6px 0 rgba(0, 0, 0, 0.108);
  --shadow-heavy: 0 25.6px 57.6px 0 rgba(0, 0, 0, 0.22), 0 4.8px 14.4px 0 rgba(0, 0, 0, 0.18);
  
  /* Переходы */
  --transition-fast: 0.1s cubic-bezier(0.1, 0.9, 0.2, 1);
  --transition-normal: 0.2s cubic-bezier(0.1, 0.9, 0.2, 1);
  --transition-slow: 0.3s cubic-bezier(0.1, 0.9, 0.2, 1);
}

/* Глобальные стили */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  background: var(--background-white);
  color: var(--text-primary);
  line-height: 1.4;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  /* Отключаем выделение текста только для декоративных элементов */
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

/* Отключаем выделение только для декоративных элементов */
.ms-hero-visual,
.decoration-circle,
.decoration-triangle {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Удаляем все контейнеры и ограничения ширины */
.full-width {
  width: 100vw;
  margin: 0;
  padding: 0;
}

.section-content {
  padding: 0 5vw;
  max-width: none;
  position: relative;
  z-index: 2;
}

/* Типографика в стиле Microsoft */
.ms-title-hero {
  font-size: 2.5rem;
  font-weight: 300;
  color: var(--text-secondary);
  margin: 2rem 0 1rem 0;
  letter-spacing: 0.05em;
  line-height: 1.3;
  font-style: italic;
}

.ms-title-section {
  font-size: clamp(1.8rem, 4vw, 3rem);
  font-weight: 600;
  line-height: 1.3;
  color: var(--text-primary);
  margin: 0 0 1rem 0;
}

.ms-subtitle {
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin: 0;
  line-height: 1.6;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
}

.ms-body {
  font-size: 1rem;
  line-height: 1.5;
  color: var(--text-secondary);
  margin: 0;
}

/* Кнопки в стиле Microsoft */
.ms-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: 1px solid var(--primary-color);
  background: var(--primary-color);
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
  text-decoration: none;
  border-radius: 2px;
  cursor: pointer;
  transition: all var(--transition-fast);
  min-width: 120px;
  text-align: center;
}

.ms-button:hover {
  background: var(--primary-hover);
  border-color: var(--primary-hover);
  transform: translateY(-1px);
  box-shadow: var(--shadow-light);
}

.ms-button:active {
  transform: translateY(0);
}

.ms-button-outline {
  background: transparent;
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.ms-button-outline:hover {
  background: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
  transform: translateY(-1px);
  box-shadow: var(--shadow-light);
}

.ms-button-large {
  padding: 1rem 2rem;
  font-size: 1rem;
  min-width: 160px;
}

/* Социальные кнопки в Microsoft стиле */
.ms-social-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border: 1px solid var(--border-medium);
  background: var(--background-white);
  color: var(--text-secondary);
  text-decoration: none;
  border-radius: 2px;
  transition: all var(--transition-fast);
  font-size: 1.2rem;
}

.ms-social-button:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
  transform: translateY(-2px);
  box-shadow: var(--shadow-light);
}

/* Сетки без блоков */
.ms-grid {
  display: grid;
  gap: 2rem;
  width: 100%;
}

.ms-grid-2 { grid-template-columns: 1fr 1fr; }
.ms-grid-3 { grid-template-columns: repeat(3, 1fr); }
.ms-grid-4 { grid-template-columns: repeat(4, 1fr); }
.ms-grid-auto { grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); }

/* Социальные ссылки без блоков */
.ms-social-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1px;
  background: var(--border-light);
}

.ms-social-item {
  background: var(--background-white);
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
  transition: all var(--transition-normal);
  cursor: pointer;
  border: none;
}

.ms-social-item:hover {
  background: var(--background-light);
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

.ms-social-icon {
  font-size: 2rem;
  color: var(--text-secondary);
  transition: color var(--transition-fast);
}

.ms-social-item:hover .ms-social-icon {
  color: var(--primary-color);
}

.ms-social-name {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.ms-social-description {
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin: 0;
}

/* Секции на полную ширину */
.ms-hero-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  position: relative;
  overflow: hidden;
  padding: 4rem 0;
}

.ms-hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 0, 0, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(0, 0, 0, 0.01) 0%, transparent 50%),
    linear-gradient(135deg, #fafafa 0%, #ffffff 100%);
  z-index: 1;
}

.ms-hero-section.parallax-bg::before {
  transform: translateY(var(--parallax-offset, 0px));
}

.ms-hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 900px;
  padding: 0 2rem;
}

/* Логотип в стиле "ГРАНИТНЫЕ ТЕХНОЛОГИИ" */
.ms-logo {
  margin-bottom: 3rem;
  display: inline-block;
}

.ms-logo-text {
  font-family: 'Times New Roman', 'Georgia', serif;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  line-height: 1;
  margin: 0;
  position: relative;
}

.ms-logo-line1 {
  font-size: 3.5rem;
  margin-bottom: 0.5rem;
  position: relative;
}

.ms-logo-line1::after {
  content: '';
  position: absolute;
  bottom: -0.2rem;
  left: 0;
  right: 0;
  height: 3px;
  background: #000000;
  z-index: 1;
}

.ms-logo-line2 {
  font-size: 2.8rem;
  margin-top: 0.3rem;
}

/* Адаптивность логотипа */
@media (max-width: 768px) {
  .ms-logo-line1 {
    font-size: 2.5rem;
  }
  
  .ms-logo-line2 {
    font-size: 2rem;
  }
  
  .ms-logo-line1::after {
    height: 2px;
  }
}

@media (max-width: 480px) {
  .ms-logo-line1 {
    font-size: 2rem;
  }
  
  .ms-logo-line2 {
    font-size: 1.6rem;
  }
}

.ms-hero-text {
  max-width: none;
}

.ms-hero-visual {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15rem;
  color: var(--border-light);
  opacity: 0.3;
}

.ms-section {
  padding: 6rem 0;
  background: var(--background-white);
}

.ms-section + .ms-section,
.ms-section + .ms-section-alt {
  padding-top: 10rem;
}

.ms-section-alt {
  background: var(--background-light);
  position: relative;
  overflow: hidden;
}

.ms-section-alt + .ms-section,
.ms-section-alt + .ms-section-alt {
  padding-top: 10rem;
}

.ms-section-alt::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 70% 30%, rgba(0, 120, 212, 0.03) 0%, transparent 50%);
  pointer-events: none;
  z-index: 1;
}

.ms-section-alt.parallax-bg::before {
  transform: translateY(var(--parallax-offset, 0px));
}

.ms-section-dark {
  background: var(--secondary-color);
  color: white;
}

.ms-section-dark .ms-title-section,
.ms-section-dark .ms-body {
  color: white;
}

.ms-section-dark .ms-subtitle {
  color: rgba(255, 255, 255, 0.8);
}

/* Карта в Microsoft стиле */
.ms-map-container {
  width: 100%;
  height: 500px;
  border: 1px solid var(--border-light);
  background: var(--background-light);
  position: relative;
}

.ms-map-info {
  position: absolute;
  top: 2rem;
  left: 2rem;
  background: var(--background-white);
  padding: 1.5rem;
  border-radius: 2px;
  box-shadow: var(--shadow-medium);
  max-width: 300px;
  z-index: 10;
}

/* Футер в Microsoft стиле */
.ms-footer {
  background: var(--secondary-color);
  color: white;
  padding: 9rem 0 1rem;
}

.ms-footer-content {
  padding: 0 5vw;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 4rem;
}

.ms-footer-brand h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: white;
}

.ms-footer-links h4 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: white;
}

.ms-footer-links ul {
  list-style: none;
}

.ms-footer-links li {
  margin-bottom: 0.5rem;
}

.ms-footer-links a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-size: 0.875rem;
  transition: color var(--transition-fast);
}

.ms-footer-links a:hover {
  color: white;
}

.ms-footer-bottom {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.875rem;
}

/* Анимации появления при скролле */
.fade-in-up {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.fade-in-up.visible {
  opacity: 1;
  transform: translateY(0);
}

.fade-in-left {
  opacity: 0;
  transform: translateX(-30px);
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.fade-in-left.visible {
  opacity: 1;
  transform: translateX(0);
}

.fade-in-right {
  opacity: 0;
  transform: translateX(30px);
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.fade-in-right.visible {
  opacity: 1;
  transform: translateX(0);
}

/* Адаптивность */
@media (max-width: 1024px) {
  .ms-hero-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }
  
  .ms-hero-visual {
    order: -1;
    font-size: 8rem;
  }
  
  .ms-footer-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .section-content {
    padding: 0 3vw;
  }
}

@media (max-width: 768px) {
  .ms-grid-2,
  .ms-grid-3,
  .ms-grid-4 {
    grid-template-columns: 1fr;
  }
  
  .ms-social-grid {
    grid-template-columns: 1fr 1fr;
  }
  
  .section-content {
    padding: 0 4vw;
  }
  
  .ms-section {
    padding: 3rem 0;
  }
  
  .ms-hero-visual {
    font-size: 6rem;
  }
}

@media (max-width: 480px) {
  .ms-social-grid {
    grid-template-columns: 1fr;
  }
  
  .section-content {
    padding: 0 6vw;
  }
}
